export default {
  data() {
    return {
      list: [
        {
          title: '主导、眼位(前五项)',
          id: 0
        },
        {
          title: '远距离、集合储备力、调节反应',
          id: 1
        },
        {
          title: '近距离',
          id: 2
        }
      ]
    }
  },
  methods: {
    handleClick(id) {
      var url = null
      switch (id) {
        case 0:
          url = 'visual_five_list' // 眼压
          break
        case 1:
          url = 'visual_remote' // 远距离
          break
        case 2:
          url = 'visual_little_remote' // 泪液检查
          break
      }
      this.$router.push({ name: url, query: { data: JSON.stringify() }})
    }
  }
}



